@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@400;700&display=swap);
iframe{display:none}html{font-family:Arial, "Helvetica Neue", Helvetica, sans-serif}label,button{cursor:pointer}.error{color:red}*{margin:0;padding:0;box-sizing:border-box}body{font-family:"Nunito", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background:#333}.App{width:500px;display:flex;flex-direction:column;justify-content:space-between;align-items:center;margin:2rem auto;padding:2.5rem 2rem;background:white;color:black;border-bottom:1px solid #333;border-radius:10px}.form{display:flex;align-items:center;flex-direction:column;justify-content:center;margin-bottom:40px}.field{margin:1rem}.button{width:100px}.line-through{text-decoration:line-through;color:#777 !important}.hide-button{display:none}

.UserInfo{font-style:italic;color:black}

.TodoInfo{width:350px;padding:8px;margin:12px 0;border:1px solid #000;border-radius:8px;background-color:lightgrey;color:red;transition:0.5s -webkit-transform ease;transition:0.5s transform ease;transition:0.5s transform ease, 0.5s -webkit-transform ease}.TodoInfo__title{margin:4px 0;font-size:inherit}.TodoInfo--completed .TodoInfo__title{color:#080}.TodoInfo:hover{-webkit-transform:translateX(10%);transform:translateX(10%)}

